'use client'

import { motion } from 'framer-motion'
import { Check, Star } from 'lucide-react'
import { scrollToSection } from '@/lib/utils'

const tiers = [
  {
    name: 'Day Pass',
    price: '$80-120',
    period: 'USD por día',
    description: 'Experimenta NUMA sin compromiso',
    features: [
      'Acceso completo por 1 día',
      'Todas las experiencias core',
      'Toallas y amenities',
      'Lounge y áreas sociales',
    ],
    cta: 'Comprar Day Pass',
    popular: false,
  },
  {
    name: 'Social',
    price: '$250',
    period: 'USD/mes',
    description: 'Para quienes buscan bienestar regular',
    features: [
      '5 clases al mes',
      'Horarios amplios 7AM-10PM',
      'Priority booking',
      '10% descuento en retail',
      'Acceso a eventos mensuales',
      '1 guest pass al mes',
    ],
    cta: 'Únete como Social',
    popular: false,
  },
  {
    name: 'Founder',
    price: '$400',
    period: 'USD/mes',
    description: 'El círculo interior de NUMA',
    features: [
      'Acceso ilimitado',
      'Sin restricción de horarios',
      'Pricing garantizado de por vida',
      'Acceso prioritario a eventos',
      'The Inner Circle + coaching',
      '3 guest passes mensuales',
      'Descuentos en medicina funcional',
      'Tu nombre en placa fundadora',
    ],
    cta: 'Ser Founder Member',
    popular: true,
    badge: 'Solo 100 spots',
  },
]

export default function Memberships() {
  return (
    <section className="py-24 md:py-32 px-4 bg-bg-dark text-white" id="memberships">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, margin: "-100px" }}
          transition={{ duration: 0.8 }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-5xl lg:text-6xl font-serif font-light text-white mb-6">
            Elige tu{' '}
            <span className="text-aura-gold font-medium italic">membresía</span>
          </h2>
          <p className="text-xl md:text-2xl text-white/70 max-w-3xl mx-auto">
            Flexibilidad para cada estilo de vida
          </p>
        </motion.div>

        {/* Pricing Cards */}
        <div className="grid md:grid-cols-3 gap-8 mb-16">
          {tiers.map((tier, index) => (
            <motion.div
              key={index}
              initial={{ opacity: 0, y: 30 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true, margin: "-100px" }}
              transition={{ delay: index * 0.15, duration: 0.6 }}
              className={`relative rounded-3xl p-8 ${
                tier.popular
                  ? 'bg-gradient-to-b from-aura-gold/20 to-transparent border-2 border-aura-gold'
                  : 'bg-white/5 border border-white/10'
              } card-hover`}
            >
              {/* Popular Badge */}
              {tier.popular && tier.badge && (
                <div className="absolute -top-4 left-1/2 transform -translate-x-1/2">
                  <div className="bg-aura-gold text-aura-black px-4 py-1.5 rounded-full flex items-center gap-2">
                    <Star className="w-4 h-4 fill-current" />
                    <span className="text-sm font-bold">{tier.badge}</span>
                  </div>
                </div>
              )}

              {/* Tier Name */}
              <h3 className="text-2xl md:text-3xl font-serif font-medium text-white mb-2">
                {tier.name}
              </h3>

              {/* Price */}
              <div className="mb-4">
                <span className="text-5xl font-serif font-light text-aura-gold">
                  {tier.price}
                </span>
                <span className="text-white/60 text-lg ml-2">{tier.period}</span>
              </div>

              {/* Description */}
              <p className="text-white/70 mb-8">
                {tier.description}
              </p>

              {/* Features */}
              <ul className="space-y-4 mb-8">
                {tier.features.map((feature, idx) => (
                  <li key={idx} className="flex items-start gap-3">
                    <div className="flex-shrink-0 w-5 h-5 rounded-full bg-aura-gold/20 flex items-center justify-center mt-0.5">
                      <Check className="w-3 h-3 text-aura-gold" strokeWidth={3} />
                    </div>
                    <span className="text-white/90 text-sm">{feature}</span>
                  </li>
                ))}
              </ul>

              {/* CTA */}
              <button
                onClick={() => scrollToSection('waitlist')}
                className={`w-full py-4 rounded-full font-semibold transition-all ${
                  tier.popular
                    ? 'bg-aura-gold text-aura-black hover:bg-aura-gold/90 btn-hover'
                    : 'bg-white/10 text-white hover:bg-white/20 border border-white/20'
                }`}
              >
                {tier.cta}
              </button>
            </motion.div>
          ))}
        </div>

        {/* Comparison Table */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ delay: 0.4, duration: 0.6 }}
          className="mt-16"
        >
          <div className="text-center mb-8">
            <h3 className="text-2xl md:text-3xl font-serif font-light text-white mb-2">
              Comparación detallada
            </h3>
            <p className="text-white/60">
              Encuentra la membresía perfecta para ti
            </p>
          </div>

          <div className="bg-white/5 border border-white/10 rounded-2xl p-6 md:p-8 overflow-x-auto">
            <table className="w-full text-left">
              <thead>
                <tr className="border-b border-white/10">
                  <th className="pb-4 text-white/90 font-medium">Beneficio</th>
                  <th className="pb-4 text-center text-white/90 font-medium">Day Pass</th>
                  <th className="pb-4 text-center text-white/90 font-medium">Social</th>
                  <th className="pb-4 text-center text-aura-gold font-medium">Founder</th>
                </tr>
              </thead>
              <tbody className="text-sm">
                {[
                  { label: 'Visitas mensuales', values: ['1 día', '5', 'Ilimitado'] },
                  { label: 'Horarios', values: ['Todos', '7AM-10PM', 'Sin restricción'] },
                  { label: 'Guest passes', values: ['-', '1/mes', '3/mes'] },
                  { label: 'Priority booking', values: ['-', '✓', '✓'] },
                  { label: 'Eventos nocturnos', values: ['-', '✓', '✓'] },
                  { label: 'The Inner Circle', values: ['-', '-', '✓'] },
                  { label: 'Pricing de por vida', values: ['-', '-', '✓'] },
                ].map((row, idx) => (
                  <tr key={idx} className="border-b border-white/5">
                    <td className="py-4 text-white/70">{row.label}</td>
                    {row.values.map((value, vIdx) => (
                      <td key={vIdx} className="py-4 text-center text-white/90">
                        {value}
                      </td>
                    ))}
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </motion.div>
      </div>
    </section>
  )
}
