'use client'

import { motion } from 'framer-motion'
import { Thermometer, Wind, Droplets, Music, Syringe, Heart } from 'lucide-react'

const experiences = [
  {
    icon: Flame,
    title: 'Temazcal Moderno',
    description: 'Ritual ancestral mexicano reimaginado con tecnología infrarroja y aromaterapia.',
    temperature: '65-75°C',
    duration: '30-45 min',
    intensity: 'Media-Alta',
    image: '/images/temazcal.jpg',
  },
  {
    icon: Wind,
    title: 'Breathwork + Ice Bath',
    description: 'Método Wim Hof y respiración holotrópica seguidos de inmersión en hielo 3-5°C.',
    temperature: '3-5°C',
    duration: '2-5 min',
    intensity: 'Alta',
    image: '/images/ice-bath.jpg',
  },
  {
    icon: Droplets,
    title: 'Cenote Therapy',
    description: 'Hidroterapia de contraste inspirada en cenotes sagrados mayas.',
    temperature: '18-22°C',
    duration: '20-30 min',
    intensity: 'Media',
    image: '/images/cenote.jpg',
  },
  {
    icon: Music,
    title: 'Sound Healing',
    description: 'Baño de gong, cuencos tibetanos y frecuencias binaurales para reseteo mental.',
    temperature: 'Ambiente',
    duration: '45-60 min',
    intensity: 'Baja',
    image: '/images/sound-healing.jpg',
  },
  {
    icon: Syringe,
    title: 'Functional IV Therapy',
    description: 'Sueros personalizados: hidratación, energía, recuperación atlética, inmunidad.',
    temperature: 'N/A',
    duration: '30-45 min',
    intensity: 'Baja',
    image: '/images/iv-therapy.jpg',
  },
  {
    icon: Heart,
    title: 'Medicina Funcional',
    description: 'Consultas con médicos funcionales, análisis de laboratorio y planes personalizados.',
    temperature: 'N/A',
    duration: '60 min',
    intensity: 'Consulta',
    image: '/images/functional-medicine.jpg',
  },
]

// Import Flame icon
import { Flame } from 'lucide-react'

export default function CoreExperiences() {
  return (
    <section className="py-24 md:py-32 px-4 bg-white" id="experiences">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, margin: "-100px" }}
          transition={{ duration: 0.8 }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-5xl lg:text-6xl font-serif font-light text-aura-black mb-6">
            Nuestras{' '}
            <span className="text-aura-gold font-medium italic">experiencias</span>
          </h2>
          <p className="text-xl md:text-2xl text-aura-black/70 max-w-3xl mx-auto">
            Cada ritual diseñado para transformar cuerpo, mente y espíritu
          </p>
        </motion.div>

        {/* Cards Grid */}
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
          {experiences.map((exp, index) => {
            const Icon = exp.icon
            return (
              <motion.div
                key={index}
                initial={{ opacity: 0, y: 30 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true, margin: "-100px" }}
                transition={{ delay: index * 0.1, duration: 0.6 }}
                className="group relative overflow-hidden rounded-2xl bg-gradient-to-br from-aura-obsidiana to-aura-black h-[400px] card-hover"
              >
                {/* Background Image Placeholder */}
                <div className="absolute inset-0 bg-gradient-to-br from-aura-terracota/30 via-aura-jade/20 to-aura-gold/20 image-hover" />

                {/* Overlay that reveals on hover */}
                <div className="absolute inset-0 bg-gradient-to-t from-aura-black via-aura-black/60 to-transparent opacity-90 group-hover:opacity-95 transition-opacity duration-300" />

                {/* Content */}
                <div className="relative h-full p-6 flex flex-col justify-between">
                  {/* Icon */}
                  <div className="flex items-start justify-between">
                    <div className="inline-flex items-center justify-center w-12 h-12 rounded-full bg-aura-gold/20 border border-aura-gold/30">
                      <Icon className="w-6 h-6 text-aura-gold" strokeWidth={1.5} />
                    </div>
                    {/* Stats badge */}
                    <div className="text-right">
                      <div className="text-aura-gold text-sm font-semibold">{exp.temperature}</div>
                      <div className="text-white/70 text-xs">{exp.duration}</div>
                    </div>
                  </div>

                  {/* Title & Description */}
                  <div>
                    <h3 className="text-2xl font-serif font-medium text-white mb-3">
                      {exp.title}
                    </h3>
                    <p className="text-white/80 text-sm leading-relaxed mb-4 opacity-0 group-hover:opacity-100 transition-opacity duration-300 transform translate-y-2 group-hover:translate-y-0">
                      {exp.description}
                    </p>
                    <div className="inline-block px-3 py-1 rounded-full bg-aura-gold/20 border border-aura-gold/40">
                      <span className="text-aura-gold text-xs font-semibold">
                        Intensidad: {exp.intensity}
                      </span>
                    </div>
                  </div>
                </div>
              </motion.div>
            )
          })}
        </div>

        {/* CTA */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ delay: 0.6, duration: 0.6 }}
          className="text-center mt-12"
        >
          <a
            href="#waitlist"
            className="inline-block btn-hover bg-aura-gold text-aura-black px-8 py-4 rounded-full font-semibold text-lg"
          >
            Reserva tu primera experiencia
          </a>
        </motion.div>
      </div>
    </section>
  )
}
